<div class="hello"><div class="ui row" v-if="!uid &amp;&amp; (!users || toList(users).length == 0)"><div class="sixteen wide column"> <div class="ui huge buttons"><button class="ui orange button" @click="loginGoogle"><i class="google icon"></i>登入</button></div></div></div><loader v-else-if="!users || toList(users).length == 0"></loader><select class="ui dropdown" v-else v-model="logic"><option value="newest">最近更新</option><option value="nearest" v-show="userLocation || (uid &amp;&amp; users[uid])">離我最近</option><option value="random">隨機介紹</option><option value="age_nearest" v-if="uid &amp;&amp; users[uid] &amp;&amp; users[uid].child_birth">孩子年齡相近</option><option v-for="(c, idx) in cities" :value="'near_' + c.c.join(',')" :key="idx">{{c.t}}附近</option></select><br><div class="ui divider"></div><br><div class="ui two doubling cards container" @scroll="handleScroll" ref="scrollContainer"><div class="ui card" v-for="(h, index) in visibleCards" :key="index"><card :h="h" :full="false" :mySearch="mySearch" :book="book" @locate="locate" @addBook="addBook" @removeBook="removeBook" :uid="uid"></card></div></div></div>